function getDate()
{
    var date = new Date();
    var year = date.getFullYear();
    var month = [ "", "", "", "", "", "",
    "", "", "", "", "", "" ];
    var month_Index =  date.getMonth();
    var date = date.getDate();
     document.getElementById('timedisplay').innerHTML = date + ' ' + month[month_Index] + ' ' + year;
}
setInterval(getDate, 0);